function getAuthCode(callback) {
    $.ajax({
        url: webnpki_config.webRelay + "/sid.sg",
        crossDomain: true,
        dataType: "text",
        type: "GET",
        data: "",
        success: function (response, status, request) {
            var responseMap = _getParameterMap(request.responseText);
            var resultCode = responseMap["resultCode"];
            var message = responseMap["message"];

            if (resultCode !== "2000") {
                KICA_ALERT.error(resultCode, message);
                return;
            }

            var validTime = responseMap["validTime"];
            var authcode = responseMap["authcode"];

            callback(authcode, validTime);
        },
        error: function (request, status, error) {
            KICA_ALERT.error(status, "ì¤‘ê°œì„œë²„ì— ìš”ì²­í•˜ëŠ” ë„ì¤‘ ì¼ì‹œì ì¸ ë¬¸ì œê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤. ë‹¤ì‹œ ì‹œë„í•˜ì„¸ìš”");
        },
    });
}

function getCert(authCode, callback) {
    $.ajax({
        url: webnpki_config.webRelay + "/cert_dn.sg",
        crossDomain: true,
        dataType: "text",
        type: "POST",
        data: "authcode=" + encodeURI(authCode),
        success: function (response, status, request) {
            var responseMap = _getParameterMap(request.responseText);
            var resultCode = responseMap["resultCode"];
            var message = responseMap["message"];

            if (resultCode !== "2000") {
                KICA_ALERT.error(resultCode, message);
                return;
            }

            var base64P12 = responseMap["pkcs12"];
            callback(base64P12);
        },
        error: function (request, status, error) {
            KICA_ALERT.error(status, "ì¤‘ê°œì„œë²„ì— ìš”ì²­í•˜ëŠ” ë„ì¤‘ ì¼ì‹œì ì¸ ë¬¸ì œê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤. ë‹¤ì‹œ ì‹œë„í•˜ì„¸ìš”");
        },
    });
}

function setCert(authCode, p12, signPkcs1, certificate, callback) {
    $.ajax({
        url: webnpki_config.webRelay + "/cert_up.sg",
        crossDomain: true,
        async: false,
        dataType: "text",
        type: "POST",
        data:
            "authcode=" +
            encodeURIComponent(authCode) +
            "&pkcs12=" +
            encodeURIComponent(p12) +
            "&signature=" +
            encodeURIComponent(signPkcs1) +
            "&certificate=" +
            encodeURIComponent(certificate.toBase64()),
        success: function (response, status, request) {
            var responseMap = _getParameterMap(request.responseText);
            var resultCode = responseMap["resultCode"];
            var message = responseMap["message"];
            if (resultCode == "2000") {
                KICA_ALERT.success("ì¸ì¦ì„œ ë‚´ë³´ë‚´ê¸°", `ì¸ì¦ ë²ˆí˜¸: '${authCode}'ë¡œ ì¸ì¦ì„œë¥¼ ì„±ê³µì ìœ¼ë¡œ ë‚´ë³´ëƒˆìŠµë‹ˆë‹¤.`);

                callback();
            } else {
                KICA_ALERT.error(resultCode, message);
            }
        },
        error: function (request, status, error) {
            KICA_ALERT.error(status, "ì¤‘ê°œì„œë²„ì— ìš”ì²­í•˜ëŠ” ë„ì¤‘ ì¼ì‹œì ì¸ ë¬¸ì œê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤. ë‹¤ì‹œ ì‹œë„í•˜ì„¸ìš”");
        },
    });
}

function _getParameterMap(message) {
    var parameters = new Object();
    var pattern = new RegExp("(\\w+)=([^}]*)", "mg");
    var matchArray;
    while ((matchArray = pattern.exec(message)) != null) {
        parameters[matchArray[1]] = matchArray[2];
    }

    return parameters;
}

module.exports = {
    getAuthCode: getAuthCode,
    getCert: getCert,
    setCert: setCert,
};
